<script>
	import Login from "./Login.svelte";
	import { slide } from 'svelte/transition';
	const url = document.location.origin;
	const days = ['Sun','Mon','Tue','Wed','Thu','Fri','Sat'];
	const months = ['January','February','March','April','May','June','July','August','September','October','November','December'];
	let shifts = {};
	let selected = new Array;
	let newShift = {};
	let wage = 6.45;
	let showForm = false;
	let username = localStorage.getItem("username") === null ? undefined : localStorage.getItem("username");
	let total = getTotal();
	$: if (username !== undefined) {
		getShifts()
	};
	
	function allChecked () {
		let all = document.getElementById('all');
		if (all.checked){
			selected = Object.keys(shifts);
		} else {
			selected = [];
		}
	}

	function addShift () {
		showForm = !showForm;
		shifts[newShift.date] = {
			"start": newShift.start,
			"end": newShift.end,
			"duration": diff(newShift.start, newShift.end),
			"status": newShift.status
		}
		newShift = {};
		total = getTotal();
		save();
	}

	function date (x) {
		let y = new Date(x);
		return `${days[y.getDay()]}, ${y.getDate()} ${months[y.getMonth()]}`
	}

	function del () {
		let temp = shifts;
		selected.forEach(shift => delete temp[shift]);
		selected = [];
		shifts = temp;
		save();
	}

	function diff (start, end) {
		start = start.split(":");
		end = end.split(":");
		var startDate = new Date(0, 0, 0, start[0], start[1], 0);
		var endDate = new Date(0, 0, 0, end[0], end[1], 0);
		var diff = endDate.getTime() - startDate.getTime();
		var hours = diff / 1000 / 60 / 60;

		// If using time pickers with 24 hours format, add the below line get exact hours
		if (hours < 0)
		hours = hours + 24;

		return hours.toFixed(2);
	}

	function editShift(shift) {
		newShift.date = shift;
		newShift.start = shifts[shift].start;
		newShift.end = shifts[shift].end;
		newShift.status = shifts[shift].status;
		showForm = true;
		selected = [];
	}

	function getTotal () {
		let a = 0.0;
		let b = selected.length === 0 ? Object.keys(shifts) : selected;
		b.forEach(shift => {
			a += parseFloat(wage*shifts[shift].duration);
		})
		return a.toFixed(2);
	}

	async function getShifts () {
		const shiftData = await fetch(`${url}/shifts?user=${username}`);
		const wageData = await fetch(`${url}/getWage?user=${username}`);
		shifts = await shiftData.json();
		wage = parseFloat(await wageData.json());
	}

	function save() {
		let req = new XMLHttpRequest();
        req.open("POST", `${url}/save`, true);
        req.setRequestHeader('Content-Type', 'application/json');
        req.send(JSON.stringify({'user':username, "data": JSON.stringify(shifts)}));
        req.onreadystatechange = () => {
            if (req.readyState === 4) {
                //console.log(req.responseText);
            }
        }
	}

	function updateStatus (x) {
		selected.forEach(shift => {shifts[shift].status = x});
		save();
		selected = [];
	}

	function updateWage() {
		let req = new XMLHttpRequest();
        req.open("POST", `${url}/setWage`, true);
        req.setRequestHeader('Content-Type', 'application/json');
        req.send(JSON.stringify({'user':username, "wage": wage}));
        req.onreadystatechange = () => {
            if (req.readyState === 4) {
                //console.log(req.responseText);
            }
		}
		total = getTotal();
	}
</script>

<svelte:head>
	<title>{username === undefined ? "Login" : "Shift Manager"}</title>
</svelte:head>

<header>
	<h3>{username === undefined ? "Login" : "Shift Manager"}</h3>
</header>

{#if username === undefined}
	<Login {url} on:login="{(msg) => {username = msg.detail.username}}"/>
{:else}
	<div transition:slide>
		<h4>📄 Your Details</h4><hr>
		<ul>
			<li>Username : <strong>{username}</strong></li>
			<li>
				<label for="wage">Your wage : £</label>
				<input type="number" step="0.01" min="0" bind:value={wage} on:change="{updateWage}" style="width: 80px">
				<span>/ hr</span>
			</li>
		</ul>
		<button style="float: right" on:click="{() => {username = undefined}}">Logout</button>

		<br>
		
		<h4>🗂 Your Shifts</h4><hr>

		{#if selected.length !== 0}
			<div transition:slide style="margin-bottom: 0.8rem;">
				<button on:click={del}>🗑</button>
				{#if selected.length === 1}
					<button on:click={() => {editShift(selected[0])}}>✏ Edit</button>
				{/if}
				<button on:click={() => {updateStatus("paid")}}>✅ Paid</button>
				<button on:click={() => {updateStatus("not-paid")}}>❌ Not Paid</button>
			</div>
		{/if}

		{#if Object.keys(shifts).length === 0}
			<pre style="text-align: center; padding:1rem;">No shifts yet</pre>
		{:else}
		<div style="overflow:auto; word-break:none;">
			<table style="white-space: nowrap;">
				<thead>
					<th style="text-align: center;">
						<input id="all" type="checkbox" checked="{selected.length === Object.keys(shifts).length}" on:change={allChecked}>
					</th>
					<th>Date</th>
					<th>Start</th>
					<th>End</th>
					<th>Duration</th>
					<th>Wage</th>
				</thead>
				<tbody>
					{#each Object.keys(shifts).sort() as shift}
						<tr class="{shifts[shift].status}">
							<td style="text-align: center;">
								<input type="checkbox" bind:group={selected} value={shift} 
								checked="{selected.includes(shift)}">
							</td>
							<td>{date(shift)}</td>
							<td>{shifts[shift].start}</td>
							<td>{shifts[shift].end}</td>
							<td>{shifts[shift].duration} hrs</td>
							<td>£ {(wage*shifts[shift].duration).toFixed(2)}</td>
						</tr>
					{/each}
				</tbody>
				<tfoot>
					<tr>
						<td colspan="6">Total : £ {selected === [] ? total : getTotal()}</td>
					</tr>
				</tfoot>
			</table>
		</div>
		{/if}
		
		<button on:click={() => {showForm = !showForm}} style="float:right">{showForm ? "Close" : "Add new Shift"}</button>

		<br>

		{#if showForm}
			<div transition:slide>
				<h4>📅 Record a New Shift</h4><hr>
				<form on:submit|preventDefault={addShift}>
					<fieldset>
						<label for="date">Date &nbsp;</label>
						<input bind:value={newShift.date} id="date" type="date" required><br><hr>
						<p style="font-size: small; color:gray">
							Please use 24 hour time format 
						</p>
						<label for="start">Start Time&nbsp;</label>
						<input bind:value={newShift.start} id="start" type="time" required><br>
						<label for="end">End Time &nbsp;&nbsp;</label>
						<input bind:value={newShift.end} id="end" type="time" required><br><hr>
						<label for="status">Wage </label>
						<select id="status" bind:value={newShift.status}>
							<option value="not-paid">❌ Not Paid</option>
							<option value="paid">✅ Paid</option>
						</select>
					</fieldset>
					<button type="submit" id="record">Record</button>
					<button type="reset">Reset</button>
				</form>
			</div>
		{/if}
	</div>
{/if}

<style>
	.paid {
		background-color: #f0fff4;
	}

	.not-paid {
		background-color: #fff5f5;
	}

	td input {
		margin:0;
	}

	button {
		margin-bottom: 0.2rem;
	}
</style>