<script>
    import { createEventDispatcher } from 'svelte';
    export let url;
    let username = localStorage.getItem("username") === null ? '' : localStorage.getItem("username");
    let password = '';
    let remember = localStorage.getItem("username") === null ? false : true;
    let eMsg;
    const dispatch = createEventDispatcher();
    
    function sendMsg () {
        dispatch('login', {"username": username});
    }

    function sendReq(path) {
        let req = new XMLHttpRequest();
        req.open("POST", `${url}/${path}`, true);
        req.setRequestHeader('Content-Type', 'application/json');
        req.send(JSON.stringify({'username':username, "password": password}));
        req.onreadystatechange = () => {
            if (req.readyState === 4) {
                if (req.status === 200) {
                    sendMsg();
                    if (remember === true) {
                        localStorage.setItem("username", username);
                    } else {
                        localStorage.removeItem("username");
                    }
                } else {
                    eMsg = req.responseText;
                }
            }
        }
    }
</script>

<form on:submit|preventDefault>
    <fieldset>
        {#if eMsg !== undefined}
            <p class="error">{eMsg}</p>
        {/if}
        <p>
            <label for="username">Username</label><br>
            <input id="username" type="text" placeholder="Enter username" bind:value={username} required>
        </p>
        <p>
            <label for="password">Password </label><br>
            <input id="password" type="password" placeholder="Enter password" bind:value={password} required>
        </p>
        <p>
            <input id="remember" type="checkbox" bind:checked={remember}>
            <span> Remember me</span>
        </p>
        <button type="submit" on:click="{() => {sendReq('login')}}">Login</button>
        <button type="submit" on:click="{() => {sendReq('signup')}}">Sign Up</button>
    </fieldset>
</form>

<style>
    .error {
        color: red;
        font-size: small;
    }
</style>